<template>
<div class="wp">
    <common-header></common-header>
    <div class="verify">
      <div class="page">
        <div class="page__bd">
           <common-logo :title="this.title"></common-logo>

           <div class="weui-cells_form">
              <div class="weui-cell underline-green lineadd">
                  <div class="weui-cell__hd"><label class="weui-label">用户名</label></div>
                  <div class="weui-cell__bd usernameinfo">{{username}}
                  </div>
              </div>
              <common-mobile @mobile="getmobile" :mobile-object="{mobile: this.mobile, country_number: this.country_number}"></common-mobile>
              <div class="weui-cell underline-green weui-cell_vcode" v-if="is_seccode">
                  <div class="weui-cell__hd"><label class="weui-label">验证码</label></div>
                  <div class="weui-cell__bd">
                      <input class="weui-input" v-validate="'required|min:4|max:6'" name="seccodeverify" v-model="seccodeverify" type="text" placeholder="请输入验证码" autocomplete="new-verify">
                  </div>
                  <div class="weui-cell__ft">
                      <common-seccode @seccodeHash="getSeccodeHash"></common-seccode>
                  </div>
              </div>
              <div class="weui-cell underline-color weui-cell_vcode">
                  <div class="weui-cell__hd"><label class="weui-label">验证码</label></div>

                  <div class="weui-cell__bd">
                      <input class="weui-input" v-validate="'required|min:4|max:6'" v-model="verify" name="verify" type="tel" placeholder="请输入短信验证码" autocomplete="new-verify">
                  </div>
                   <div class="weui-cell__ft">
                      <button @click="getSmsCode" v-bind:disabled="send_status" class="weui-vcode-btn diycolor">{{send_msg}}</button>
                  </div>
              </div>
              <a href="javascript:;" v-on:click="setverify" class="weui-btn weui-btn_primary diybgcolor">确认修改</a>
              <div class="weui-flex">
                  <a href="./"><div class="weui-flex__item"><div class="placeholder diycolor">返回首页</div></div></a>
                  <div class="weui-flex__item"><div class="placeholder"></div></div>
                  <div class="weui-flex__item"><div class="placeholder"></div></div>
              </div>
             <!--  <div class="weui-flex">
                  <div><div class="placeholder">社交账号登录</div></div>
                  <div class="weui-flex__item"><div class="placeholder">QQ登录 微信登录</div></div>
                  <div><div class="placeholder"></div></div>
              </div> -->
          </div>         
        </div>
      </div>
  </div>
  <common-footer></common-footer>
</div>
</template>

<script>
import CommonHeader from './common/Header'
import CommonFooter from './common/Footer'
import CommonSeccode from './common/Seccode'
import CommonMobile from './common/Mobile'
import CommonLogo from './common/Logo'
export default {
  components: {
    CommonHeader,
    CommonFooter,
    CommonSeccode,
    CommonMobile,
    CommonLogo
  },
  name: 'verify',
  data () {
    return {
      title: '绑定手机',
      username: '',
      send_status: false,
      send_msg: '获取验证码',
      time: 60,
      verify: '',
      email: '',
      seccodeverify: '',
      mobile: '',
      seccode: '',
      code: '',
      is_seccode: '',
      country_number: '',
      seccodeHash: ''
    }
  },
  methods: {
    setverify: function () {
      let _this = this
      if (!this.mobile) {
        this.weui.topTips('请输入正确的手机号')
      } else {
        this.$http.post(this.baseUrl + 'plugin.php?id=zhanmishu_sms:verify&send_verify_Button=yes&method=submit&from=mobileverify&formhash=' + this.$_G.formhash, this.qs.stringify({seccodeverify: this.seccodeverify, seccodehash: this.seccodeHash, mobile: this.mobile, code: this.code, verify: this.verify, isnew: 1})).then(
          response => {
            if (response.data.code < 1) {
              _this.weui.topTips(response.data.msg)
            } else {
              _this.weui.toast('操作成功', 2000)
              this.finshAndBack()
            }
          },
          response => {
            console.log('verify error')
          }
        )
      }
    },
    getSmsCode: function () {
      this.code = this.getRandNum()
      this.send_status = true
      this.time = 60
      let _this = this
      this.$http.post(this.baseUrl + 'plugin.php?id=zhanmishu_sms:verify&no_submit=yes&method=send&formhash=' + this.$_G.formhash, this.qs.stringify({seccodeverify: this.seccodeverify, seccodehash: this.seccodeHash, mobile: this.mobile, username: this.username, code: this.code, nationcode: this.country_number.substring(1)})).then(
        response => {
          if (parseInt(response.data.code) === 1) {
            for (let i = 0; i <= 60; i++) {
              window.setTimeout(function () {
                if (_this.time !== 0) {
                  _this.send_msg = _this.time + '秒后重发验证'
                  _this.time--
                } else {
                  _this.send_status = false
                  _this.send_msg = '获取验证码'
                }
              }, i * 1000)
            }
          } else {
            if (response.data.msg) {
              _this.weui.topTips(response.data.msg)
            } else {
              let substr = response.data.match(/<div\s+class="jump_c">\s+<p>(.*)<\/p>/)
              _this.weui.topTips(substr[1])
            }
            _this.send_status = false
            _this.send_msg = '获取验证码'
          }
        },
        response => {
          _this.send_status = false
          _this.send_msg = '获取验证码'
        }
      )
    },
    getRandNum: function () {
      return Math.round(900000 * Math.random() + 100000)
    },
    getSeccodeHash: function (seccodeHash) {
      this.seccodeHash = seccodeHash
    },
    getmobile: function (mobileObj) {
      this.country_number = mobileObj.country_number
      this.mobile = mobileObj.mobile
    },
    setScecode: function () {
      let obj = this
      if (this.$_G) {
        this.username = this.$_G.username
        if (!this.username) {
          this.$router.push('login')
        }
        if (this.$_G.mobile) {
          this.mobile = this.$_G.mobile.replace('+' + obj.$_G.nationcode, '')
        }
        if (this.$_G.nationcode) {
          this.country_number = '+' + this.$_G.nationcode
        }
      } else {
        setTimeout(function () {
          if (obj.$_G) {
            obj.username = obj.$_G.username
            if (!obj.$_G.username) {
              obj.$router.push('login')
            }
            if (obj.$_G.mobile) {
              obj.mobile = obj.$_G.mobile.replace('+' + obj.$_G.nationcode, '')
            }
            if (obj.$_G.nationcode) {
              obj.country_number = '+' + obj.$_G.nationcode
            }
          } else {
            obj.setScecode()
          }
        }, 1)
      }
    },
    finshAndBack: function () {
      if (document.referrer.indexOf(this.baseUrl) >= 0 && document.referrer.indexOf('zhanmishu_sms:register') < 0 && document.referrer.indexOf('member.php') < 0 && document.referrer.indexOf('connect.php') < 0) {
        self.location.href = document.referrer
      } else {
        self.location.href = this.baseUrl
      }
    }
  },
  mounted: function () {
    this.updateG()
    this.setScecode()
    this.setTitle('绑定手机')
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
input {
    border:0;
    padding-left: 10px;
    border-radius: 4px;
    color: #333;
    -webkit-appearance: none;
    height: 43px;
  }
.verify {padding:0 20px;}
.lineadd {line-height: 43px;}
.underline-green {border-bottom:1px solid #e6e5e5; }
.weui-cell {padding:5px 0 0 0;}
.usernameinfo {text-align: left;}
.weui-vcode-img {display: block;min-width: 113px;padding-left: 10px;}
.weui-cell:before {border: 0;}
.weui-vcode-btn {border-left: 0}
.weui-btn_primary {color: #fff;margin-top: 25px;margin-bottom: 15px;}
.agreement-tip {color: #807e7e}
.weui-vcode-btn {width: 150px;}
</style>



