<template>
<div class="common_seccode">
	<img v-on:click="changeVerify" class="weui-vcode-img" :src="verifyImg" height="45px" width="150px"  autocomplete="new-password">
</div>
</template>

<script>
export default {
  name: 'seccode',
  data () {
    return {
      seccodeHash: '',
      randNum: 0,
      verifyImg: ''
    }
  },
  mounted: function () {
    this.setScecode()
  },
  methods: {
    getSeccodeUrl: function () {
      this.seccodeHash = this.$_G.sechash
      this.getRandNum()
      this.verifyImg = this.baseUrl + 'misc.php?mod=seccode&update=' + this.randNum + '&idhash=' + this.seccodeHash
      this.$emit('seccodeHash', this.seccodeHash)
    },
    getRandNum: function () {
      this.randNum = Math.round(900000 * Math.random() + 100000)
    },
    changeVerify: function () {
      this.getSeccodeUrl()
    },
    setScecode: function () {
      var obj = this
      if (this.$_G) {
        obj.getSeccodeUrl()
      } else {
        setTimeout(function () {
          if (obj.$_G) {
            obj.getSeccodeUrl()
          } else {
            obj.setScecode()
          }
        }, 1)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.common_seccode img {width: 95%;height: 95%;}
</style>




