import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Register from '@/components/Register'
import Password from '@/components/Password'
import Verify from '@/components/Verify'
import VerifyEdit from '@/components/VerifyEdit'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Register
    },
    {
      path: '/Register',
      name: 'Register',
      component: Register
    },
    {
      path: '/Login',
      name: 'Login',
      component: Login
    },
    {
      path: '/Password',
      name: 'Password',
      component: Password
    },
    {
      path: '/Verify',
      name: 'Verify',
      component: Verify
    },
    {
      path: '/VerifyEdit',
      name: 'VerifyEdit',
      component: VerifyEdit
    }
  ]
})



