<template>
<div class="common_timer">
     <div class="weui-cell__ft">
        <button @click="getSmsCode" v-bind:disabled="timerStatus.send_status" class="weui-vcode-btn diycolor">{{timerStatus.send_msg}}</button>
    </div></div>
</template>

<script>
export default {
  name: 'timer',
  data () {
    return {
    }
  },
  props: ['timerStatus'],
  methods: {
    getSmsCode: function () {
      this.$emit('getSmsCode', '')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.weui-vcode-btn {border-left: 0;}
</style>




