<template>
<div class="wp">
    <common-header></common-header>
    <div class="password">
      <div class="page">
        <div class="page__bd">
           <common-logo :title="this.title"></common-logo>

           <div class="weui-cells_form">

              <common-mobile @mobile="getmobile" :mobile-object="{mobile: this.mobile, country_number: this.country_number}"></common-mobile>
              <div class="weui-cell underline-green" v-if="is_seccode">
                  <div class="weui-cell__hd"><label class="weui-label">验证码</label></div>
                  <div class="weui-cell__bd">
                      <input class="weui-input" v-validate="'required|min:4|max:6'" name="seccodeverify" v-model="seccodeverify" type="text" placeholder="请输入验证码" autocomplete="new-password">
                  </div>
                  <div class="weui-cell__ft">
                      <common-seccode @seccodeHash="getSeccodeHash"></common-seccode>
                  </div>
              </div>
              <div class="weui-cell underline-green">
                  <div class="weui-cell__hd"><label class="weui-label">验证码</label></div>
                  <div class="weui-cell__bd">
                      <input class="weui-input" v-validate="'required|min:4|max:6'" v-model="verify" name="verify" type="tel" placeholder="请输入短信验证码" autocomplete="new-password">
                  </div>
                   <div class="weui-cell__ft">
                      <button @click="getSmsCode" v-bind:disabled="send_status" class="weui-vcode-btn diycolor">{{send_msg}}</button>
                  </div>
              </div>
              <div class="weui-cell underline-green">
                  <div class="weui-cell__hd"><label class="weui-label">验证码</label></div>
                  <div class="weui-cell__bd">
                      <input class="weui-input" v-validate="'required|min:6|max:32'" name="password" v-model="password" type="password" pattern="[0-9]*" placeholder="请输入密码" autocomplete="new-password">
                  </div>
              </div>
              <a href="javascript:;" v-on:click="getpassword" class="weui-btn weui-btn_primary diybgcolor">确认修改</a>
              <div class="weui-flex">
                  <router-link :to="{ path: '/register'}" replace><div class="weui-flex__item"><div class="placeholder diycolor">立即注册</div></div></router-link>
                  <div class="weui-flex__item"><div class="placeholder"></div></div>
                  <div class="weui-flex__item"><div class="placeholder"></div></div>
                  <router-link :to="{ path: '/login'}" replace><div class="weui-flex__item"><div class="placeholder diycolor">直接登陆</div></div></router-link>
              </div>
             <!--  <div class="weui-flex">
                  <div><div class="placeholder">社交账号登录</div></div>
                  <div class="weui-flex__item"><div class="placeholder">QQ登录 微信登录</div></div>
                  <div><div class="placeholder"></div></div>
              </div> -->
          </div>         
        </div>
      </div>
  </div>
  <common-footer></common-footer>
</div>
</template>

<script>
import CommonHeader from './common/Header'
import CommonFooter from './common/Footer'
import CommonSeccode from './common/Seccode'
import CommonMobile from './common/Mobile'
import CommonLogo from './common/Logo'
export default {
  components: {
    CommonHeader,
    CommonFooter,
    CommonSeccode,
    CommonLogo,
    CommonMobile
  },
  name: 'password',
  data () {
    return {
      title: '找回密码',
      username: '',
      send_status: false,
      send_msg: '获取验证码',
      time: 60,
      password: '',
      email: '',
      verify: '',
      seccodeverify: '',
      mobile: '',
      seccode: '',
      code: '',
      is_seccode: '',
      country_number: '',
      seccodeHash: ''
    }
  },
  methods: {
    getpassword: function () {
      if (!this.mobile) {
        this.weui.topTips('请输入正确的手机号')
      } else {
        this.$http.post(this.baseUrl + 'plugin.php?id=zhanmishu_sms:getpassword&no_submit=yes&method=getpasswordsubmit&formhash=' + this.$_G.formhash, this.qs.stringify({seccodeverify: this.seccodeverify, seccodehash: this.seccodeHash, mobile: this.mobile, code: this.code, verify: this.verify, password: this.password})).then(
          response => {
            // this.finshAndBack()
          },
          response => {
            console.log('password error')
          }
        )
      }
    },
    getSmsCode: function () {
      this.code = this.getRandNum()
      this.send_status = true
      this.time = 60
      let _this = this
      this.$http.post(this.baseUrl + 'plugin.php?id=zhanmishu_sms:getpassword&no_submit=yes&method=getpasswordsend&formhash=' + this.$_G.formhash, this.qs.stringify({seccodeverify: this.seccodeverify, seccodehash: this.seccodeHash, mobile: this.mobile, username: this.username, code: this.code})).then(
        response => {
          if (parseInt(response.data.code) === 1) {
            for (let i = 0; i <= 60; i++) {
              window.setTimeout(function () {
                if (_this.time !== 0) {
                  _this.send_msg = _this.time + '秒后重发验证'
                  _this.time--
                } else {
                  _this.send_status = false
                  _this.send_msg = '获取验证码'
                }
              }, i * 1000)
            }
          } else {
            if (response.data.msg) {
              _this.weui.topTips(response.data.msg)
            } else {
              let substr = response.data.match(/<div\s+class="jump_c">\s+<p>(.*)<\/p>/)
              _this.weui.topTips(substr[1])
            }
            _this.send_status = false
            _this.send_msg = '获取验证码'
          }
        },
        response => {
          _this.send_status = false
          _this.send_msg = '获取验证码'
        }
      )
    },
    getRandNum: function () {
      return Math.round(900000 * Math.random() + 100000)
    },
    getSeccodeHash: function (seccodeHash) {
      this.seccodeHash = seccodeHash
    },
    getmobile: function (mobileObj) {
      this.country_number = mobileObj.country_number
      this.mobile = mobileObj.mobile
    },
    setScecode: function () {
      var obj = this
      console.log('尝试验证码')
      if (this.$_G !== undefined) {
        this.is_seccode = this.$_G.rule.password.allow > 0 ? '1' : ''
      } else {
        setTimeout(function () {
          if (obj.$_G !== undefined) {
            obj.is_seccode = obj.$_G.rule.password.allow > 0 ? '1' : ''
          } else {
            obj.setScecode()
          }
        }, 1)
      }
    },
    finshAndBack: function () {
      if (document.referrer.indexOf(this.baseUrl) >= 0 && document.referrer.indexOf('zhanmishu_sms:register') < 0 && document.referrer.indexOf('member.php') < 0 && document.referrer.indexOf('connect.php') < 0) {
        self.location.href = document.referrer
      } else {
        self.location.href = this.baseUrl
      }
    }
  },
  mounted: function () {
    this.setScecode()
    this.setTitle('找回密码')
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
input {
    border:0;
    padding-left: 10px;
    border-radius: 4px;
    color: #333;
    -webkit-appearance: none;
    height: 43px;
  }
.password {padding: 0 20px;}

.weui-cell {padding:5px 0 0 0;}
.underline-green {border-bottom:1px solid #e6e5e5; }
.weui-vcode-img {display: block;min-width: 113px;padding-left: 10px;}
.weui-cell:before {border: 0;}
.weui-vcode-btn {border-left: 0}
.weui-btn_primary {color: #fff;margin-top: 25px;margin-bottom: 15px;}
.agreement-tip {color: #807e7e}
</style>



