<template>

<div class="wp">
    <common-header></common-header>
    <div class="register">
      <div class="page">
        <div class="page__bd">
           <common-logo :title="this.title"></common-logo>
           <div class="weui-cells_form">
              <div class="weui-cell underline-green">
                <div class="weui-cell__hd"><label class="weui-label">用户名</label></div>
                <div class="weui-cell__bd">
                      <input class="weui-input" v-validate="'required|min:2|max:12'" name="username" v-model="username" type="text" pattern="[\w]*" placeholder="请填写用户名" autocomplete="new-password">
                </div>
              </div>
              <div class="weui-cell underline-green">
                  <div class="weui-cell__hd"><label class="weui-label">密&nbsp;&nbsp;&nbsp;&nbsp;码</label></div>
                  <div class="weui-cell__bd">
                      <input class="weui-input" v-validate="'required|min:2|max:32'" name="password" v-model="password" type="password" pattern="[0-9]*" placeholder="请填写密码" autocomplete="new-password">
                  </div>
              </div>
              <common-mobile @mobile="getmobile" :mobile-object="{mobile: this.mobile, country_number: this.country_number}"></common-mobile>
              <div class="weui-cell weui-cell_vcode  underline-green" v-if="is_seccode">
                  <div class="weui-cell__hd"><label class="weui-label">图形验证码</label></div>
                  <div class="weui-cell__bd">
                      <input class="weui-input" v-validate="'required|min:4|max:6'" name="seccodeverify" v-model="seccodeverify" type="text" placeholder="请填写验证码">
                  </div>
                  <div class="weui-cell__ft">
                      <common-seccode @seccodeHash="getSeccodeHash"></common-seccode>
                  </div>
              </div>
              <div class="weui-cell weui-cell_vcode underline-color">
                  <div class="weui-cell__hd"><label class="weui-label">短信验证码</label></div>
                  <div class="weui-cell__bd">
                      <input class="weui-input" v-validate="'required|min:4|max:6'" v-model="verify" name="verify" type="tel" placeholder="请填写验证码">
                  </div>
                   <div class="weui-cell__ft">
                      <button @click="getSmsCode" v-bind:disabled="send_status" class="weui-vcode-btn diycolor">{{send_msg}}</button>
                  </div>
              </div>
              <a href="javascript:;" v-on:click="register" class="weui-btn weui-btn_primary diybgcolor">注册</a>
<!--               <div class="weui-cell">
                <div class="weui-flex">
                  <div class="weui-flex__item"><p class="agreement-tip">点击「注册」按钮，即代表你同意<a href="/" target="_blank">《网站协议》</a></p></div>
                </div>
              </div> -->
              <div class="weui-flex">
                  <router-link :to="{ path: '/login'}" replace><div class="weui-flex__item"><div class="placeholder diycolor">直接登录</div></div></router-link>
                  <div class="weui-flex__item"><div class="placeholder"></div></div>
                  <div class="weui-flex__item"><div class="placeholder"></div></div>
                  <router-link :to="{ path: '/password'}" replace><div class="weui-flex__item"><div class="placeholder diycolor">找回密码</div></div></router-link>
              </div>
             <!--  <div class="weui-flex">
                  <div><div class="placeholder">社交账号登录</div></div>
                  <div class="weui-flex__item"><div class="placeholder">QQ登录 微信登录</div></div>
                  <div><div class="placeholder"></div></div>
              </div> -->
          </div>         
        </div>
      </div>
  </div>
  <common-footer></common-footer>
</div>
</template>

<script>
import CommonHeader from './common/Header'
import CommonFooter from './common/Footer'
import CommonSeccode from './common/Seccode'
import CommonMobile from './common/Mobile'
import CommonLogo from './common/Logo'
export default {
  components: {
    CommonHeader,
    CommonFooter,
    CommonSeccode,
    CommonLogo,
    CommonMobile
  },
  name: 'register',
  data () {
    return {
      username: '',
      title: '用户注册',
      send_status: false,
      send_msg: '获取验证码',
      time: 10,
      password: '',
      email: '',
      verify: '',
      seccodeverify: '',
      mobile: '',
      seccode: '',
      code: '',
      is_seccode: '',
      country_number: '',
      seccodeHash: ''
    }
  },
  methods: {
    register: function () {
      if (!this.mobile) {
        this.weui.topTips('请输入正确的手机号')
      } else {
        this.$validator.validateAll().then((result) => {
          if (this.$validator.errors.items[0]) {
            this.weui.topTips(this.$validator.errors.items[0].msg)
          } else {
            this.$http.post(this.baseUrl + 'plugin.php?id=zhanmishu_sms:send&registersubmit=yes&method=register&formhash=' + this.$_G.formhash, this.qs.stringify({verify: this.verify, seccodeverify: this.seccodeverify, seccodehash: this.seccodeHash, username: this.username, mobile: this.mobile, code: this.code, passwd: this.password})).then(
              response => {
                if (parseInt(response.data.code) === 1) {
                  this.finshAndBack()
                } else {
                  if (response.data.msg) {
                    this.weui.topTips(response.data.msg)
                  } else {
                  }
                  this.updateG()
                }
              },
              response => {
              }
            )
          }
        })
      }
    },
    getSmsCode: function () {
      this.code = this.getRandNum()
      this.send_status = true
      this.time = 60
      let _this = this
      this.$http.post(this.baseUrl + 'plugin.php?id=zhanmishu_sms:send&no_submit=yes&method=send&formhash=' + this.$_G.formhash, this.qs.stringify({seccodeverify: this.seccodeverify, seccodehash: this.seccodeHash, phone: this.mobile, code: this.code})).then(
        response => {
          if (parseInt(response.data.code) === 1) {
            for (let i = 0; i <= 60; i++) {
              window.setTimeout(function () {
                if (_this.time !== 0) {
                  _this.send_msg = _this.time + '秒后重发验证'
                  _this.time--
                } else {
                  _this.send_status = false
                  _this.send_msg = '获取验证码'
                }
              }, i * 1000)
            }
          } else {
            if (response.data.msg) {
              _this.weui.topTips(response.data.msg)
            } else {
              let substr = response.data.match(/<div\s+class="jump_c">\s+<p>(.*)<\/p>/)
              _this.weui.topTips(substr[1])
            }
            _this.send_status = false
            _this.send_msg = '获取验证码'
          }
        },
        response => {
          _this.send_status = false
          _this.send_msg = '获取验证码'
        }
      )
    },
    getRandNum: function () {
      return Math.round(900000 * Math.random() + 100000)
    },
    getSeccodeHash: function (seccodeHash) {
      this.seccodeHash = seccodeHash
    },
    getmobile: function (mobileObj) {
      console.log(mobileObj)
      this.country_number = mobileObj.country_number
      this.mobile = mobileObj.mobile
    },
    setScecode: function () {
      var obj = this
      if (obj.$_G !== undefined) {
        obj.is_seccode = obj.$_G.rule.register.allow > 0 ? '1' : ''
        if (obj.$_G.username) {
          obj.finshAndBack()
        }
      } else {
        setTimeout(function () {
          if (obj.$_G !== undefined) {
            obj.is_seccode = obj.$_G.rule.register.allow > 0 ? '1' : ''
            if (obj.$_G.username) {
              obj.finshAndBack()
            }
          } else {
            obj.setScecode()
          }
        }, 1)
      }
    },
    finshAndBack: function () {
      if (document.referrer.indexOf(this.baseUrl) >= 0 && document.referrer.indexOf('zhanmishu_sms:register') < 0 && document.referrer.indexOf('member.php') < 0 && document.referrer.indexOf('connect.php') < 0) {
        self.location.href = document.referrer
      } else {
        self.location.href = this.baseUrl
      }
    }
  },
  mounted: function () {
    this.setScecode()
    this.setTitle('用户注册')
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
.register {padding: 0 20px;}
input {
    border:0;
    padding-left: 10px;
    border-radius: 4px;
    color: #333;
    -webkit-appearance: none;
    height: 43px;
  }
.weui-cell {padding:5px 0 0 0;}
.underline-green {border-bottom:1px solid #e6e5e5; }
.weui-vcode-img {display: block;min-width: 113px;padding-left: 10px;}
.weui-cell:before {border: 0;}
.weui-vcode-btn {border-left: 0}
.weui-cell_vcode {}
.weui-btn_primary {color: #fff;margin-top: 25px;margin-bottom: 15px;}
.agreement-tip {color: #807e7e}
</style>



