<template>
<div class="page_hd">
  <div class="page_title">
    <!--  -->
    {{title}}
  </div>
  <div>
    <img @click="finshAndBack" :src="closeIcoon" alt="" class="closeIcoon">
  </div>
</div>
</template>

<script>
export default {
  name: 'title',
  data () {
    return {
      closeIcoon: this.baseUrl + 'source/plugin/zhanmishu_sms/template/touch/img/close.svg'
    }
  },
  props: ['title'],
  methods: {
    finshAndBack: function () {
      if (document.referrer.indexOf(this.baseUrl) >= 0 && document.referrer.indexOf('zhanmishu_sms:register') < 0 && document.referrer.indexOf('member.php') < 0) {
        self.location.href = document.referrer
      } else {
        self.location.href = this.baseUrl
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.closeIcoon {width: 20px;height: 20px;position: fixed;top: 10px;left: 10px;}
.page_hd{padding: 30px 15px;}
.page_title{
    text-align: left;
    font-size: 26px;
    font-weight: 400;
}
</style>




