<template>
<div class="wp">
    <common-header></common-header>
    <div class="login">
      <div class="page">
        <div class="page__bd">
           <common-logo :title="this.title"></common-logo>
           <div class="weui-flex login_type">
              <div class="weui-flex__item"><div class="placeholder"></div></div>
              <div class="weui-flex__item" @click="check_login_type('username')" v-bind:class="{item: isUsername }"><div class="placeholder">账号登录</div></div>
              <div class="weui-flex__item" @click="check_login_type('mobile')" v-bind:class="{item: isMobile }"><div class="placeholder">短信登录</div></div>
              <div class="weui-flex__item"><div class="placeholder"></div></div>
           </div>
           <div class="weui-cells_form">
              <div class="weui-cell underline-green"  v-if="login_type === 'username'">
                  <div class="weui-cell__hd"><label class="weui-label">用户名</label></div>
                  <div class="weui-cell__bd">
                      <input class="weui-input" v-validate="'required|min:2|max:12'" name="username" v-model="username" type="text" pattern="[\w]*" placeholder="请填写用户名/手机号">
                  </div>
              </div>
              <div class="weui-cell underline-green" v-if="login_type === 'username'">
                  <div class="weui-cell__hd"><label class="weui-label">密&nbsp;&nbsp;&nbsp;&nbsp;码</label></div>

                  <div class="weui-cell__bd">
                      <input class="weui-input" v-validate="'required|min:1|max:32'" name="password" v-model="password" type="password" pattern="[0-9]*" placeholder="请输入密码">
                  </div>
              </div>
              <div class="weui-cell underline-green weui-cell_select" v-if="login_type === 'username' && isquestion">
                <div class="weui-cell__bd">
                  <input class="weui-input checkQuestionid" id="checkQuestionid" @click="checkQuestionid" name="questionid" v-model="questionid" type="text" pattern="" placeholder="安全提问(未设置请忽略)">
                </div>
              </div>
              <div class="weui-cell underline-green" v-if="questionvalue !== '0'">
                  <div class="weui-cell__bd">
                      <input class="weui-input" v-validate="'required|min:1|max:12'" name="answer" v-model="answer" type="text" pattern="[\w]*" placeholder="请输入安全问答答案">
                  </div>
              </div>
              <common-mobile  v-if="login_type === 'mobile'" @mobile="getmobile" :mobile-object="{mobile: this.mobile, country_number: this.country_number}"></common-mobile>
              <div class="weui-cell underline-green" v-if="is_seccode">
                  <div class="weui-cell__hd"><label class="weui-label">验证码</label></div>
                  <div class="weui-cell__bd">
                      <input class="weui-input" v-validate="'required|min:4|max:6'" name="seccodeverify" v-model="seccodeverify" type="text" placeholder="请输入验证码">
                  </div>
                  <div class="weui-cell__ft">
                      <common-seccode @seccodeHash="getSeccodeHash"></common-seccode>
                  </div>
              </div>
              <div class="weui-cell underline-color"  v-if="login_type === 'mobile'">
                  <div class="weui-cell__hd"><label class="weui-label">验证码</label></div>
                  <div class="weui-cell__bd">
                      <input class="weui-input" v-validate="'required|min:4|max:6'" v-model="verify" name="verify" type="tel" placeholder="请输入短信验证码">
                  </div>
                   <div class="weui-cell__ft">
                      <button @click="getSmsCode" v-bind:disabled="send_status" class="weui-vcode-btn diycolor">{{send_msg}}</button>
                  </div>
              </div>
              <a href="javascript:;" v-on:click="login" class="weui-btn weui-btn_primary diybgcolor">登录</a>
              <div class="weui-flex">
                  <router-link :to="{ path: '/register'}" replace><div class="weui-flex__item diycolor"><div class="placeholder">立即注册</div></div></router-link>
                  <div class="weui-flex__item"><div class="placeholder"></div></div>
                  <div class="weui-flex__item"><div class="placeholder"></div></div>
                  <router-link :to="{ path: '/password'}" replace><div class="weui-flex__item diycolor"><div class="placeholder">无法登录</div></div></router-link>
              </div>
              <div class="weui-flex other-loginp">
                  <div><div class="placeholder label">三方登录&nbsp;&nbsp;</div></div>
                  <div class="weui-flex__item">
                    <div class="placeholder other-login">
                      <div v-html="this.login_html"></div>
                    </div>
                  </div>
                  <div><div class="placeholder"></div></div>
              </div>
          </div>         
        </div>
      </div>
  </div>
  <common-footer></common-footer>
</div>
</template>

<script>
import CommonHeader from './common/Header'
import CommonFooter from './common/Footer'
import CommonSeccode from './common/Seccode'
import CommonMobile from './common/Mobile'
import CommonLogo from './common/Logo'

export default {
  components: {
    CommonHeader,
    CommonFooter,
    CommonSeccode,
    CommonLogo,
    CommonMobile
  },
  name: 'login',
  data () {
    return {
      title: '用户登录',
      username: '',
      mobilebuttoncolor: '',
      send_status: false,
      send_msg: '获取验证码',
      time: 60,
      password: '',
      questionid: '安全提问(未设置请忽略)',
      questionvalue: '0',
      answer: '',
      email: '',
      verify: '',
      seccodeverify: '',
      mobile: '',
      seccode: '',
      code: '',
      is_seccode: false,
      isquestion: true,
      login_html: '',
      country_number: '',
      seccodeHash: '',
      login_type: 'username',
      isUsername: true,
      isMobile: false
    }
  },
  methods: {
    login: function () {
      if (this.login_type === 'mobile' && !this.mobile) {
        this.weui.topTips('请输入正确的手机号')
      } else if (this.login_type === 'mobile' && this.mobile) {
        this.$http.post(this.baseUrl + 'plugin.php?id=zhanmishu_sms:verify&loginsubmit=yes&method=login&formhash=' + this.$_G.formhash, this.qs.stringify({seccodeverify: this.seccodeverify, seccodehash: this.seccodeHash, mobile: this.mobile, code: this.code, verify: this.verify})).then(
          response => {
            this.weui.loading('正在登录')
            let _this = this
            _this.updateG()
            if (response.data.code < 1) {
              this.weui.topTips(response.data.msg)
            } else {
              this.weui.toast('登录成功', {
                duration: 1000,
                className: '',
                callback: function () {
                  _this.finshAndBack()
                }
              })
            }
            this.weui.loading('正在登录').hide()
          },
          response => {
          }
        )
      } else if (this.login_type === 'username' && this.username) {
        this.$validator.validateAll().then((result) => {
          if (this.$validator.errors.items[0]) {
            this.weui.topTips(this.$validator.errors.items[0].msg)
          } else {
            this.$http.post(this.baseUrl + 'member.php?mod=logging&action=login&loginsubmit=yes&handlekey=login&loginhash=' + this.$_G.loginhash + '&inajax=1&formhash=' + this.$_G.formhash, this.qs.stringify({seccodeverify: this.seccodeverify, seccodehash: this.seccodeHash, username: this.username, password: this.password, answer: this.answer, questionid: this.questionvalue})).then(
              response => {
                let substr = response.data.match(/window\.location\.href\s=\s'(.*)'/)
                if (substr && substr[1]) {
                  this.weui.loading('正在登录')
                  let _this = this
                  _this.updateG()
                  this.weui.toast('登录成功', {
                    duration: 1000,
                    className: '',
                    callback: function () {
                      _this.finshAndBack()
                    }
                  })
                  this.weui.loading('正在登录').hide()
                } else {
                  let substrerr = response.data.match(/<dt\sid="messagetext">\s+<p>(.*)<script/)
                  if (substrerr) {
                    this.weui.topTips(substrerr[1])
                  } else {
                    this.weui.topTips('登录失败')
                  }
                }
              },
              response => {
              }
            )
          }
        })
      }
      this.weui.topTips(this.$validator.errors.items[0].msg)
    },
    checkQuestionid: function () {
      var obj = this
      this.weui.picker(
        [
          {
            label: '安全提问(未设置请忽略)',
            value: '0'
          },
          {
            label: '母亲的名字',
            value: '1'
          },
          {
            label: '爷爷的名字',
            value: '2'
          },
          {
            label: '父亲出生的城市',
            value: '3'
          },
          {
            label: '您其中一位老师的名字',
            value: '4'
          },
          {
            label: '您个人计算机的型号',
            value: '5'
          },
          {
            label: '您最喜欢的餐馆名称',
            value: '6'
          },
          {
            label: '驾驶执照最后四位数字',
            value: '7'
          }
        ],
        {
          className: 'checkQuestionid',
          defaultValue: [1],
          onChange: function (result) {
          },
          onConfirm: function (result) {
            obj.questionvalue = result[0].value
            obj.questionid = result[0].label
          },
          id: 'checkQuestionid'
        }
      )
    },
    check_login_type: function (type) {
      this.login_type = type
      if (type === 'username') {
        this.isUsername = true
        this.isMobile = false
      } else {
        this.isUsername = false
        this.isMobile = true
      }
    },
    getSmsCode: function () {
      this.code = this.getRandNum()
      this.send_status = true
      this.time = 60
      let _this = this
      this.$http.post(this.baseUrl + 'plugin.php?id=zhanmishu_sms:verify&no_submit=yes&method=loginsend&formhash=' + this.$_G.formhash + '&code=' + this.code, this.qs.stringify({seccodeverify: this.seccodeverify, seccodehash: this.seccodeHash, phone: this.mobile})).then(
        response => {
          if (parseInt(response.data.code) === 1) {
            for (let i = 0; i <= 60; i++) {
              window.setTimeout(function () {
                if (_this.time !== 0) {
                  _this.send_msg = _this.time + '秒后重发验证'
                  _this.time--
                } else {
                  _this.send_status = false
                  _this.send_msg = '获取验证码'
                }
              }, i * 1000)
            }
          } else {
            if (response.data.msg) {
              _this.weui.topTips(response.data.msg)
            } else {
              let substr = response.data.match(/<div\s+class="jump_c">\s+<p>(.*)<\/p>/)
              _this.weui.topTips(substr[1])
            }
            _this.send_status = false
            _this.send_msg = '获取验证码'
          }
        },
        response => {
          _this.send_status = false
          _this.send_msg = '获取验证码'
        }
      )
    },
    getRandNum: function () {
      return Math.round(900000 * Math.random() + 100000)
    },
    getSeccodeHash: function (seccodeHash) {
      this.seccodeHash = seccodeHash
    },
    getmobile: function (mobileObj) {
      this.country_number = mobileObj.country_number
      this.mobile = mobileObj.mobile
    },
    setScecode: function () {
      var obj = this
      if (this.$_G) {
        this.is_seccode = this.$_G.rule.login.allow > 0 ? '1' : ''
        this.login_html = this.$_G.login_html
        if (obj.$_G.username) {
          obj.finshAndBack()
        }
      } else {
        setTimeout(function () {
          if (obj.$_G) {
            obj.login_html = obj.$_G.login_html
            obj.is_seccode = obj.$_G.rule.login.allow > 0 ? '1' : ''
            obj.isquestion = obj.$_G.isquestion > 0 ? '1' : ''
            if (obj.$_G.username) {
              obj.finshAndBack()
            }
          } else {
            obj.setScecode()
          }
        }, 1)
      }
    },
    finshAndBack: function () {
      if (document.referrer.indexOf(this.baseUrl) >= 0 && document.referrer.indexOf('zhanmishu_sms:register') < 0 && document.referrer.indexOf('member.php') < 0 && document.referrer.indexOf('connect.php') < 0) {
        self.location.href = document.referrer
      } else {
        self.location.href = this.baseUrl
      }
    }
  },
  mounted: function () {
    this.updateG()
    this.setScecode()
    this.setTitle('用户登录')
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

input {
    border:0;
    padding-left: 10px;
    border-radius: 4px;
    color: #333;
    -webkit-appearance: none;
    height: 43px;
  }
.weui-cell {padding:5px 0 0 0;}
.login_type {margin-bottom: 20px;}
.login {padding: 0 20px;}
.underline-green {border-bottom:1px solid #e6e5e5; }
.weui-vcode-img {display: block;min-width: 113px;padding-left: 10px;}
.weui-cell:before {border: 0;}
.weui-vcode-btn {border-left: 0}
.weui-btn_primary {color: #fff;margin-top: 25px;margin-bottom: 15px;}
.agreement-tip {color: #807e7e}
.other-loginp {margin-top: 10px;}
.other-loginp .label {color: #807e7e;}
.other-login {text-align: left;}
.weui-flex__item {}
.weui-flex__item.item {border-bottom: 3px solid ;}
</style>



