// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import axios from 'axios'

let baseUrl = () => {
  let url = window.location.href.toString()
  let routerIndex = url.lastIndexOf('#')
  let last = url.substring(0, routerIndex).lastIndexOf('/')
  return url.substring(0, routerIndex).substring(0, last) + '/'
}

let setTitle = (title) => {
  document.title = title
  let mobile = navigator.userAgent.toLowerCase()
  if (/iphone|ipad|ipod/.test(mobile)) {
    let iframe = document.createElement('iframe')
    iframe.style.visibility = 'hidden'
    // 替换成站标favicon路径或者任意存在的较小的图片即可
    iframe.setAttribute('src', baseUrl() + 'favicon.ico')
    let iframeCallback = function () {
      setTimeout(function () {
        iframe.removeEventListener('load', iframeCallback)
        document.body.removeChild(iframe)
      }, 0)
    }
    iframe.addEventListener('load', iframeCallback)
    document.body.appendChild(iframe)
  }
}

export default{
  install (Vue, options) {
    Vue.prototype.baseUrl = baseUrl()
    Vue.prototype.setTitle = setTitle
    axios.get(baseUrl() + 'plugin.php?id=zhanmishu_sms:register&action=common').then(
    response => {
      Vue.prototype.$_G = response.data
    },
    response => { console.log(response) }
    )
    Vue.prototype.updateG = (callback) => {
      axios.get(baseUrl() + 'plugin.php?id=zhanmishu_sms:register&action=common&randnum=' + Math.round(900000 * Math.random() + 100000)).then(
      response => {
        if (typeof callback === 'function') {
          callback()
        }
        Vue.prototype.$_G = response.data
      },
      response => { console.log(response) }
      )
    }
  }
}



