<template>
<div class="weui-cell underline-green">
  <div class="weui-cell__hd" v-if="isnation">
      <input @click="checkCountry" class="weui-select" name="country_number" v-model="mobileObject.country_number">
  </div>
  <div class="weui-cell__hd"><label class="weui-label">手机号</label></div>
  <div class="weui-cell__bd">
      <input class="weui-input" v-on:change="inputMoble" v-validate="'required|min:8|max:11'"  v-model="mobileObject.mobile" type="tel" name="mobile" placeholder="请输入手机号" autocomplete="new-password">
  </div>
</div>
</template>

<script>
export default {
  name: 'mobile',
  data () {
    return {
      nations: [],
      isnation: false
    }
  },
  props: ['mobileObject'],
  mounted: function () {
    this.setNations()
  },
  methods: {
    inputMoble: function () {
      this.$emit('mobile', this.mobileObject)
    },
    checkCountry: function () {
      var obj = this
      this.weui.picker(
        this.nations,
        {
          className: 'checkCountry',
          defaultValue: [1],
          onChange: function (result) {
          },
          onConfirm: function (result) {
            obj.mobileObject.country_number = '+' + result[0].value
            obj.$emit('mobile', obj.mobileObject)
          },
          id: 'checkCountry'
        }
      )
    },
    setNations: function () {
      var obj = this
      if (obj.$_G) {
        obj.nations = obj.$_G.nationcode
      } else {
        setTimeout(function () {
          if (obj.$_G) {
            obj.nations = obj.$_G.nationcode
          } else {
            obj.setNations()
          }
        }, 1)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.weui-input{    border:0;border-radius: 4px;height: 43px;color: #333;padding-left: 10px;    -webkit-appearance: none;}
.underline-green {border-bottom:1px solid #e6e5e5; }
.weui-cell {padding:5px 0 0 0;}

</style>




